export default {
	data() {
		return {
			phone:'',
			rPhone:'',
			remarks:'',
			idCard:'',
			showrPhone:false,
			showIdCard:false,
		}
	},
	onLoad() {
		let self=this;
		uni.showLoading({
			title:'正在请求'
		});
		self.$axios(self.$baseUrl.getRealInfo,{})
		.then(res=>{
			uni.hideLoading();
			if(res.data.code==1){
				self.phone=res.data.data.mobile;
				self.rPhone=res.data.data.pat_mobile;
				self.remarks=res.data.data.remarks;
				self.idCard=res.data.data.idcard;
				if(res.data.data.pat_mobile){
					self.showrPhone = true;
				}
				if(res.data.data.idcard){
					self.showIdCard = true;
				}
				
			}else{
				self.$api.msg(res.data.msg);
			}
		},(error) =>{
			uni.hideLoading();
			console.log(error)
		})	
	},
	methods: {
		
		save(){
			let self =this;
			if(!self.phone){
				uni.showToast({title:'请输入手机号',icon:'none'});
				return ;
			}else{
				if(!(/^1[345678]\d{9}$/.test(self.phone))){ 
					uni.showToast({title:'请输入正确的手机号',icon:'none'});
					return false; 
				} 
			}
			if(!self.rPhone){
				uni.showToast({title:'请输入邀请人手机号',icon:'none'});
				return ;
			}
			if(!self.idCard){
				uni.showToast({title:'请输入身份证号',icon:'none'});
				return ;
			}
			if(self.rPhone == self.phone ){
				uni.showToast({title:'推荐人不能是自己',icon:'none'});
				return ;
			}
		
			uni.showLoading({
				title:"正在提交"
			})
			//验证这些参数
			
			self.$axios(self.$baseUrl.setRealName,{
				mobile:self.phone,
				recommend_mobile:self.rPhone,
				remarks:self.remarks,
				idcard:self.idCard,
			}).then(res =>{
				uni.hideLoading();
				
				if(res.data.code==1){
					uni.showToast({title:'绑定成功',icon:'success'});
					uni.navigateBack({
						animationDuration:60 * 10000
					})
					
				
					
				}else{
					self.$api.msg(res.data.msg);
				}
			},(error) =>{
				uni.hideLoading()
				console.log(error)
			})
		}
	}
}
